
:root {
  --blue-color: #10253F;
  --blush-color: #FFCCCC;
  --melon-color: #F7B2B7;
  --white-color: #FCFFFC;
  --teal-color: #5bc0be;
  --gold-color: #b8834e;
}
@font-face {
  font-family: 'Salmela';
  src: url('assets/fonts/salmela.otf') format('opentype');
  font-weight: normal;
  font-style: normal;
}
body, html {
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
  background-color: var(--white-color);
  color: var(--blue-color);
}
#app {
  font-family: 'Courgette', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
#main {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
}
section {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -ms-flex-line-pack: center;
      align-content: center;
  height: 100vh;
  text-align: center;
  background: transparent;
}
a {
  color: var(--gold-color);
}
a:clicked {
  color: var(--gold-color);
}
h1, h2, h3, h4, h5, h6 {
  font-family: 'Salmela', serif;
}
h2 {
  font-size: 2em;
  color: var(--gold-color);
}
h3 {
  color: var(--gold-color);
}
@media (min-width: 500px) {
section {
    background: none;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
}
.section-background {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}
.section-content {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -ms-flex-line-pack: center;
        align-content: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    width: 40%;
    background: none;
}
}



