
#background {
  position: fixed;
  bottom: 0;
  height: 5rem;
  width: 100%;
  z-index: -1;
}
#fade {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  background: -webkit-gradient(linear, left top, left bottom, from(rgb(255, 255, 255, 1)), to(rgba(0, 0, 0, 0)));
  background: linear-gradient(to bottom, rgb(255, 255, 255, 1), rgba(0, 0, 0, 0));
}
#image {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 100%;
  z-index: -2;
  background-image: url('../assets/images/lights_crop.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  -webkit-transform: scaleX(-1);
          transform: scaleX(-1);
}



